<template>
  <div class="c_select_img">
    <P class="title">应用信息</P>
    <div class="app_cover_content_logo">
      <p class="app_cover_content_l_des app_cover_content_l_des_logo">应用logo：</p>
      <div class="app_cover_content_logowrap">
        <span v-if="!inputmodel.icon" class="app_cover_content_logowrap_text">LOGO</span>
        <img v-if="inputmodel.icon" :src="inputmodel.icon" alt />
        <input type="file" @change="inputChange" accept="image/*" />
      </div>
      <p class="app_cover_content_des">建议使用750*750，1M以内的JPG、PNG图片</p>
    </div>
    <div class="app_cover_content_name">
      <p class="app_cover_content_l_des">应用名称：</p>
      <input type="text" placeholder="请输入应用名称" v-model="inputmodel.appName" />
    </div>
    <div class="app_cover_content_con">
      <p class="app_cover_content_l_des">应用介绍：</p>
      <textarea name="" placeholder="输入应用介绍" v-model="inputmodel.appDes" id="" cols="30" rows="10"></textarea>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    inputmodel: {
      type: Object,
      required: true
    }
  },
  methods: {
    inputChange(e) {
      console.log('Input框改变');
      console.log(e);
      var size = (e.target.files[0].size / 1024 / 1024).toFixed(2);
      if (size > 1) {
        // 文件大于1M
        this.$zero.ToastWarning('图片的大小大于1M，请另行选择');
        return ;
      }

      let file = e.target.files[0]; // 单个文件对象
      let BlobUrl = this.$zero.fileToBlobUrl(file);
      console.log('生成的图片的地址');
      console.log(BlobUrl);
      // this.inputmodel.icon = BlobUrl;
      this.$set(this.inputmodel, 'icon', BlobUrl);
      this.$set(this.inputmodel, 'isBase64', false); // 设置它不是base64位图片
      // 将文件拿过来
      this.$set(this.inputmodel, 'imgFile', file); // 把图片保存下来
    }
  }
};
</script>
<style scoped>
.c_select_img {
  border-bottom: 1px solid #e6e6e6;
}
.title {
  padding-top: 35px;
  font-weight: bold;
}
.app_cover_content {
  padding: 0 20px;
}
.app_cover_content_logo {
  display: flex;
  padding-top: 10px;
}
.app_cover_content_l_des {
  width: 120px;
  color: #999999;
}
.app_cover_content_l_des_logo {
  padding-top: 10px;
}
.app_cover_content_logowrap {
  position: relative;
  display: flex;
  width: 60px;
  height: 60px;
  border: 1px solid #e6e6e6;
  text-align: center;
  line-height: 60px;
}
.app_cover_content_logowrap_text{
  width: 100%;
}
.app_cover_content_logowrap input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  cursor: pointer;
}
.app_cover_content_logowrap img {
  width: 100%;
  height: 100%;
}
.app_cover_content_des {
  line-height: 60px;
  margin-left: 10px;
  color: #999999;
}
.app_cover_content_name {
  display: flex;
  align-items: center;
  margin-top: 20px;
}
.app_cover_content_name input {
  box-sizing: border-box;
  width: 350px;
  height: 35px;
  padding-left: 8px;
  border: 1px solid #e6e6e6;
}
.app_cover_content_con {
  display: flex;
  margin-top: 20px;
  padding-bottom: 45px;
}
.app_cover_content_con textarea {
  box-sizing: border-box;
  width: 350px;
  height: 100px;
  padding: 10px 8px;
  border: 1px solid #e6e6e6;
  outline: none;
  resize: none;
}
</style>
