<template>
  <div class="c_select_img">
    <p class="c_select_img_title">应用分类</p>
    <div class="c_select_wrap clearfix">
      <div class="c_select_wrap_des fl">
        所属分类
      </div>
      <div class="c_select_wrap_item fl">
        <el-radio v-model="radio.typeid" :label="item._id" v-for="item in radioList" :key="item._id">{{item._name}}</el-radio>
        <!-- <el-radio v-model="radio" label="1">备选项</el-radio>
        <el-radio v-model="radio" label="2">备选项</el-radio> -->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    radio: { // 当前选择的值 这个值由父组件传递而来吧
      type: Object,
      required: true
    }
  },
  data() {
    return {
      radioList: []
    }
  },
  methods: {
    // 初始化获取应用分类
    async getAppPlace() {
      // 获取企业id
      let company_id = this.$zero.getCompany_id();
      if (!company_id) {
        return this.$zero.ToastError('企业id获取失败，请稍后再试');
      }

      let placeList = await this.$api.application.getTypeList({
        company_id: company_id
      });
      if (placeList.statusCode === 75500) {
        return this.$zero.ToastError('服务繁忙，获取应用分类错误');
      }
      if (placeList.statusCode !== 75200) {
        return this.$zero.ToastError(`获取应用分类错误，错误码:${placeList.statusCode}`);
      }

      // 获取成功
      console.log('获取到的场地列表');
      console.log(placeList);
      this.radioList = placeList.types;
      // 默认选中第一个
      this.radio.typeid = this.radioList[0]._id;
    }
  },
  created() {
    this.getAppPlace();
  }
}
</script>
<style scoped>
.c_select_img {
  padding-top: 40px;
}
.c_select_img_title {
  font-weight: bold;
}
.c_select_wrap_des {
  margin-right: 30px;
  color: #999999;
}
.c_select_wrap {
  margin-top: 20px;
  padding-bottom: 90px;
  border-bottom: 1px solid #d9d9d9;
}
</style>
